import api from './api';

const activityLogService = {
  // Rcuprer tous les logs d'activit avec filtrage
  getLogs: async (filters = {}) => {
    try {
      const params = new URLSearchParams();
      
      // Ajouter les filtres  la requte
      Object.entries(filters).forEach(([key, value]) => {
        if (value !== undefined && value !== null && value !== '') {
          params.append(key, value);
        }
      });
      
      const response = await api.get(`/activity-logs/logs/?${params.toString()}`);
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la rcupration des logs d\'activit:', error);
      throw error;
    }
  },

  // Rcuprer un log d'activit spcifique
  getLog: async (id) => {
    try {
      const response = await api.get(`/activity-logs/logs/${id}/`);
      return response.data;
    } catch (error) {
      console.error(`Erreur lors de la rcupration du log d'activit #${id}:`, error);
      throw error;
    }
  },

  // Rcuprer les statistiques des logs d'activit
  getStatistics: async () => {
    try {
      const response = await api.get('/activity-logs/logs/statistics/');
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la rcupration des statistiques:', error);
      throw error;
    }
  },

  // Rcuprer la liste des modules disponibles
  getModules: async () => {
    try {
      const response = await api.get('/activity-logs/logs/modules/');
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la rcupration des modules:', error);
      throw error;
    }
  },

  // Rcuprer la liste des types d'action disponibles
  getActionTypes: async () => {
    try {
      const response = await api.get('/activity-logs/logs/action-types/');
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la rcupration des types d\'action:', error);
      throw error;
    }
  },

  // Rcuprer la liste des niveaux de svrit
  getSeverities: async () => {
    try {
      const response = await api.get('/activity-logs/logs/severities/');
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la rcupration des niveaux de svrit:', error);
      throw error;
    }
  },

  // Supprimer des logs d'activit (rserv aux fondateurs)
  clearLogs: async (data) => {
    try {
      const response = await api.post('/activity-logs/logs/clear/', data);
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la suppression des logs:', error);
      throw error;
    }
  }
};

export default activityLogService;
