import React from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { useRole } from '../contexts/RoleContext';

/**
 * Composant de protection de route bas sur l'authentification et les rles
 * @param {Object} props - Proprits du composant
 * @param {React.ReactNode} props.children - Composant enfant  rendre si l'accs est autoris
 * @param {string} [props.requiredRole] - Rle minimum requis pour accder  la route
 * @param {string} [props.requiredPermission] - Permission spcifique requise pour accder  la route
 * @returns {React.ReactNode} - Le composant enfant ou une redirection
 */
const ProtectedRoute = ({ 
  children, 
  requiredRole, 
  requiredPermission 
}) => {
  const { isAuthenticated, loading } = useAuth();
  const { userRole, hasRole, hasPermission } = useRole();
  const location = useLocation();

  // Si l'authentification est en cours de chargement ou si l'utilisateur est authentifi mais que son rle n'est pas encore dtermin, afficher un indicateur de chargement
  if (loading || (isAuthenticated() && userRole === null)) {
    return (
      <div className="loading-container">
        <div className="loading-spinner"></div>
        <p>Chargement...</p>
      </div>
    );
  }

  // Vrifier si l'utilisateur est authentifi
  if (!isAuthenticated()) {
    // Rediriger vers la page de connexion avec l'URL actuelle comme destination aprs connexion
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  // Vrifier si l'utilisateur a le rle requis
  if (requiredRole && !hasRole(requiredRole)) {
    // Rediriger vers une page d'accs refus
    return <Navigate to="/acces-refuse" replace />;
  }

  // Vrifier si l'utilisateur a la permission requise
  if (requiredPermission && !hasPermission(requiredPermission)) {
    // Rediriger vers une page d'accs refus
    return <Navigate to="/acces-refuse" replace />;
  }

  // Si toutes les vrifications sont passes, rendre le composant enfant
  return children;
};

export default ProtectedRoute;
