import api from './api';

const API_URL = '/accounts/management/'; // Correspond  l'URL enregistre dans le routeur Django

const userService = {
  /**
   * Rcupre la liste de tous les utilisateurs.
   * @returns {Promise<AxiosResponse<any>>}
   */
  getUsers: () => {
    return api.get(API_URL);
  },

  /**
   * Cre un nouvel utilisateur.
   * @param {object} userData - Les donnes du nouvel utilisateur.
   * @returns {Promise<AxiosResponse<any>>}
   */
  createUser: (userData) => {
    return api.post(API_URL, userData);
  },

  /**
   * Met  jour les informations d'un utilisateur.
   * @param {string} id - L'ID de l'utilisateur.
   * @param {object} userData - Les donnes  mettre  jour.
   * @returns {Promise<AxiosResponse<any>>}
   */
  updateUser: (id, userData) => {
    return api.patch(`${API_URL}${id}/`, userData);
  },

  /**
   * Supprime un utilisateur.
   * @param {string} id - L'ID de l'utilisateur.
   * @returns {Promise<AxiosResponse<any>>}
   */
  deleteUser: (id) => {
    return api.delete(`${API_URL}${id}/`);
  },

  /**
   * Met  jour le rle d'un utilisateur.
   * @param {string} id - L'ID de l'utilisateur.
   * @param {string} role - Le nouveau rle.
   * @returns {Promise<AxiosResponse<any>>}
   */
  updateRole: (id, role) => {
    return api.patch(`${API_URL}${id}/update-role/`, { role });
  },

  /**
   * Rinitialise le mot de passe d'un utilisateur (action admin).
   * @param {string} id - L'ID de l'utilisateur.
   * @param {string} new_password - Le nouveau mot de passe.
   * @returns {Promise<AxiosResponse<any>>}
   */
  resetPassword: (id, new_password) => {
    return api.post(`${API_URL}${id}/set-password/`, { new_password });
  },
  /**
   * Rcupre la liste des utilisateurs avec le rle livreur.
   * @param {object} params - Paramtres optionnels (pagination, etc.)
   * @returns {Promise<AxiosResponse<any>>}
   */
  getDeliveryUsers: (params = {}) => {
    return api.get(`${API_URL}?role=livreur`, { params });
  },
};

export default userService;
