import api from './api';

// Mapping des rles backend vers les rles frontend
const ROLE_MAPPING = {
  'fondateur': 'fondateur',
  'administrateur': 'administrateur',
  'manager': 'manager',
  'gestionnaire': 'gestionnaire',
  'livreur': 'livreur'
};

const authService = {
  // Rcuprer le profil complet de l'utilisateur
  fetchUserProfile: async () => {
    try {
      // Utiliser le chemin correct vers l'endpoint du profil utilisateur
      const response = await api.get('/accounts/me/');
      if (response.data) {
        // Dterminer le rle en fonction des attributs utilisateur
        let user = response.data;
        
        if (!user.role) {
          if (user.is_superuser) {
            user.role = 'fondateur';
          } else if (user.is_staff) {
            user.role = 'administrateur';
          } else {
            // Rle par dfaut si non spcifi
            user.role = 'gestionnaire';
          }
        }
        
        // Normaliser le rle selon notre mapping
        user.role = ROLE_MAPPING[user.role.toLowerCase()] || 'gestionnaire';
        
        // Sauvegarder les donnes utilisateur compltes
        localStorage.setItem('admin_user', JSON.stringify(user));
        return user;
      }
      return null;
    } catch (error) {
      console.error('Erreur lors de la rcupration du profil utilisateur:', error);
      throw error;
    }
  },

  // Connecter un administrateur
  login: async (username, password) => {
    try {
      // L'API Django attend 'username', pas 'email'
      const response = await api.post('/v1/token-auth/', { username, password });
      if (response.data && response.data.token) {
        localStorage.setItem('admin_token', response.data.token);
        
        // Rcuprer les donnes utilisateur depuis la rponse
        let user = response.data.user;
        
        // Si la rponse ne contient pas d'utilisateur, crer un objet utilisateur basique
        if (!user) {
          user = {
            username: username,
            is_staff: true,
            is_superuser: false,
            role: 'gestionnaire' // Rle par dfaut
          };
        }
        
        // Sauvegarder temporairement les donnes utilisateur basiques
        localStorage.setItem('admin_user', JSON.stringify(user));
        
        // Rcuprer le profil complet de l'utilisateur
        try {
          const profileUser = await authService.fetchUserProfile();
          if (profileUser) {
            user = profileUser; // Utiliser les donnes compltes du profil
          }
        } catch (profileError) {
          console.warn('Impossible de rcuprer le profil complet:', profileError);
          
          // Continuer avec les donnes basiques si le profil complet n'est pas disponible
          // Dterminer le rle en fonction des attributs utilisateur
          if (!user.role) {
            if (user.is_superuser) {
              user.role = 'fondateur';
            } else if (user.is_staff) {
              user.role = 'administrateur';
            } else {
              // Rle par dfaut si non spcifi
              user.role = 'gestionnaire';
            }
          }
          
          // Normaliser le rle selon notre mapping
          user.role = ROLE_MAPPING[user.role.toLowerCase()] || 'gestionnaire';
          
          localStorage.setItem('admin_user', JSON.stringify(user));
        }
      }
      return response.data;
    } catch (error) {
      console.error('Erreur lors de la connexion:', error);
      throw error;
    }
  },

  // Dconnecter un administrateur
  logout: () => {
    localStorage.removeItem('admin_token');
    localStorage.removeItem('admin_user');
  },

  // Vrifier si un utilisateur est connect
  isAuthenticated: () => {
    return !!localStorage.getItem('admin_token');
  },

  // Rcuprer l'utilisateur connect
  getCurrentUser: () => {
    const userStr = localStorage.getItem('admin_user');
    if (userStr) {
      try {
        return JSON.parse(userStr);
      } catch (e) {
        return null;
      }
    }
    return null;
  },

  // Vrifier si l'utilisateur est un administrateur
  isAdmin: () => {
    const user = authService.getCurrentUser();
    return user && (user.is_staff || user.is_superuser || user.role === 'administrateur' || user.role === 'fondateur');
  },
  
  // Obtenir le rle de l'utilisateur
  getUserRole: () => {
    const user = authService.getCurrentUser();
    if (!user) return null;
    
    // Dterminer le rle en fonction des attributs utilisateur
    if (user.role) {
      return ROLE_MAPPING[user.role.toLowerCase()] || 'gestionnaire';
    }
    
    if (user.is_superuser) {
      return 'fondateur';
    } else if (user.is_staff) {
      return 'administrateur';
    }
    
    // Rle par dfaut
    return 'gestionnaire';
  },

  // Vrifier le token et rcuprer les informations de l'utilisateur
  checkAuthStatus: async () => {
    try {
      // Utiliser le token existant pour vrifier l'authentification
      // Comme nous n'avons pas d'endpoint spcifique, nous utilisons les donnes stockes
      const user = authService.getCurrentUser();
      
      if (!user) {
        throw new Error('Utilisateur non authentifi');
      }
      
      return user;
    } catch (error) {
      console.error('Erreur lors de la vrification du statut d\'authentification:', error);
      authService.logout();
      throw error;
    }
  }
};

export default authService;
