import api from './api';

const API_URL = '/v1/orders/livraisons/'; // Endpoint pour les livraisons

const deliveryService = {
  /**
   * Rcupre les livraisons assignes au livreur connect.
   * @returns {Promise<AxiosResponse<any>>}
   */
  getMyDeliveries: () => {
    return api.get(API_URL); // Le ViewSet filtre automatiquement pour le livreur connect
  },

  /**
   * Rcupre toutes les livraisons avec pagination (pour admin/fondateur).
   * @param {number} page - Numro de page.
   * @param {number} limit - Nombre d'lments par page.
   * @param {string} search - Terme de recherche.
   * @param {string} status - Filtre par statut.
   * @returns {Promise<AxiosResponse<any>>}
   */
  getAllDeliveries: (page = 1, limit = 10, search = '', status = '') => {
    let params = {
      page,
      limit,
    };
    
    if (search) {
      params.search = search;
    }
    
    if (status) {
      params.statut = status;
    }
    
    return api.get(API_URL, { params });
  },

  /**
   * Met  jour le statut d'une livraison.
   * @param {string} deliveryId - L'ID de la livraison.
   * @param {string} status - Le nouveau statut.
   * @returns {Promise<AxiosResponse<any>>}
   */
  updateDeliveryStatus: (deliveryId, status) => {
    return api.patch(`${API_URL}${deliveryId}/`, { statut: status });
  },

  /**
   * Rcupre les dtails d'une livraison.
   * @param {string} deliveryId - L'ID de la livraison.
   * @returns {Promise<AxiosResponse<any>>}
   */
  getDeliveryDetails: (deliveryId) => {
    return api.get(`${API_URL}${deliveryId}/`);
  },

  /**
   * Dmarre une livraison.
   * @param {string} deliveryId - L'ID de la livraison.
   * @returns {Promise<AxiosResponse<any>>}
   */
  startDelivery: (deliveryId) => {
    return api.patch(`${API_URL}${deliveryId}/start_delivery/`);
  },

  /**
   * Termine une livraison avec succs.
   * @param {string} deliveryId - L'ID de la livraison.
   * @param {object} deliveryData - Les donnes de confirmation de livraison.
   * @returns {Promise<AxiosResponse<any>>}
   */
  completeDelivery: (deliveryId, deliveryData = {}) => {
    return api.patch(`${API_URL}${deliveryId}/complete_delivery/`, deliveryData);
  },

  /**
   * Signale un problme de livraison.
   * @param {string} deliveryId - L'ID de la livraison.
   * @param {object} issueData - Les donnes du problme.
   * @returns {Promise<AxiosResponse<any>>}
   */
  reportDeliveryIssue: (deliveryId, issueData) => {
    // Mettre  jour le statut et ajouter les notes
    return api.patch(`${API_URL}${deliveryId}/`, {
      statut: 'echec',
      notes_livreur: issueData.reason || 'Problme signal'
    });
  }
};

export default deliveryService;
